body {
  font-family: SamsungOne-Regular, sans-serif;
  font-size: 13px;
  font-weight: 300;
}

.oui-page-title {
  text-align: center;
}

h1 {
  padding: 1em 0;
  font-weight: 400;
  font-size: 40px !important;
}

h2 {
  font-size: 1.6em;
  font-weight: 500;
}

h3 {
  font-size: 1.4em;
  font-weight: 500;
}

p {
  line-height: 1.4em;
}

a {
  color: var(--app-accent);
}

a:hover, a:active {
  color: var(--active);
}

:root {
  --oui-tab-height: 90px;
  --nav-height: 60px;
  --glow-height: 13px;
  --margin-s: 16px;
  --margin-m: 24px;
  --margin-l: 32px;
  --s-handset: 240px;
  --m-handset: 360px;
  --l-handset: 480px;
  --s-tablet: 600px;
  --m-tablet: 720px;
  --l-tablet: 840px;
  --desktop: 960px;
  --easeInCubic: cubic-bezier(.55, .055, .675, .19);
  --easeOutCubic: cubic-bezier(.215, .61, .355, 1);
  --easeInOutCubic: cubic-bezier(.645, .045, .355, 1);
  --easeInCirc: cubic-bezier(.6, .04, .98, .335);
  --easeOutCirc: cubic-bezier(.075, .82, .165, 1);
  --easeInOutCirc: cubic-bezier(.785, .135, .15, .86);
  --easeInExpo: cubic-bezier(.95, .05, .795, .035);
  --easeOutExpo: cubic-bezier(.19, 1, .22, 1);
  --easeInOutExpo: cubic-bezier(1, 0, 0, 1);
  --easeInQuad: cubic-bezier(.55, .085, .68, .53);
  --easeOutQuad: cubic-bezier(.25, .46, .45, .94);
  --easeInOutQuad: cubic-bezier(.455, .03, .515, .955);
  --easeInQuart: cubic-bezier(.895, .03, .685, .22);
  --easeOutQuart: cubic-bezier(.165, .84, .44, 1);
  --easeInOutQuart: cubic-bezier(.77, 0, .175, 1);
  --easeInQuint: cubic-bezier(.755, .05, .855, .06);
  --easeOutQuint: cubic-bezier(.23, 1, .32, 1);
  --easeInOutQuint: cubic-bezier(.86, 0, .07, 1);
  --easeInSine: cubic-bezier(.47, 0, .745, .715);
  --easeOutSine: cubic-bezier(.39, .575, .565, 1);
  --easeInOutSine: cubic-bezier(.445, .05, .55, .95);
  --easeInBack: cubic-bezier(.6, -.28, .735, .045);
  --easeOutBack: cubic-bezier(.175, .885, .32, 1.275);
  --easeInOutBack: cubic-bezier(.68, -.55, .265, 1.55);
  --circle-size: 50px;
}

.container {
  max-width: 95%;
  margin: 0 auto;
}

.scroll-container {
  scrollbar-gutter: stable both-edges;
  height: 100%;
  overflow-y: overlay;
  overscroll-behavior: none;
  -webkit-overflow-scrolling: auto;
  height: calc(100vh - var(--oui-tab-height, 60px)  - env(safe-area-inset-bottom));
}

.scroll-container::-webkit-scrollbar {
  width: 3px;
}

.scroll-container::-webkit-scrollbar-track {
  background: none;
}

.scroll-container::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-accent);
  border-radius: 3px;
}

.no-scroll {
  height: 100vh;
  overflow: hidden;
}

.top-glow {
  width: 100%;
  height: var(--glow-height);
  pointer-events: none;
  background: var(--top-glow-effect);
  position: fixed;
  top: 0;
  left: 0;
}

.bottom-glow {
  left: 0;
  right: 0;
  bottom: calc(var(--nav-height));
  height: var(--glow-height);
  pointer-events: none;
  background: var(--bottom-glow-effect);
  position: fixed;
}

.tab-section {
  display: none;
}

.tab-section.active {
  display: block;
}

.oui-loading-no-interaction {
  pointer-events: none !important;
  user-select: none !important;
}

.oui-loading-mask {
  background: var(--loading-mask);
  opacity: 0;
  pointer-events: none;
  z-index: 1000;
  transition: opacity .5s;
  position: fixed;
  inset: 0;
}

.oui-loading-circle {
  width: var(--circle-size);
  height: var(--circle-size);
  background: var(--loading-circle);
  z-index: 1001;
  opacity: 0;
  pointer-events: none;
  border-radius: 50%;
  transition: opacity .5s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.oui-loading-circle.active {
  opacity: 1;
  pointer-events: all;
}

.oui-loading {
  --dot: 8px;
  --r-min: 7px;
  --r-max: 15px;
  --spin: 1s;
  --pulse: 1s;
  --blue: #0080fc;
  --green: #00d490;
  width: 10px;
  height: 10px;
  animation: spin var(--spin) linear infinite;
  will-change: transform;
  z-index: 1002;
  animation: spin var(--spin) linear infinite;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.arm {
  width: 0;
  height: 0;
  transform: rotate(var(--angle));
  transform-origin: 0 0;
  position: absolute;
  top: 50%;
  left: 50%;
}

.ball {
  width: var(--dot);
  height: var(--dot);
  left: calc(-1 * var(--dot) / 2);
  top: calc(-1 * var(--dot) / 2);
  transform: translateX(var(--r-min));
  animation: radiusPulse var(--pulse) ease-in-out infinite;
  will-change: transform;
  border-radius: 50%;
  position: absolute;
}

.ball.blue {
  background: var(--blue);
}

.ball.green {
  background: var(--green);
}

@keyframes spin {
  0% {
    transform: translate(-50%, -50%)rotate(0);
  }

  100% {
    transform: translate(-50%, -50%)rotate(360deg);
  }
}

@keyframes radiusPulse {
  0%, 100% {
    transform: translateX(var(--r-min));
  }

  50% {
    transform: translateX(var(--r-max));
  }
}

@media (prefers-reduced-motion: reduce) {
  .oui-loading, .ball {
    animation: none;
  }
}

.oui-loading-mask, .oui-loading {
  opacity: 0;
  pointer-events: none;
  transition: opacity .5s;
}

.oui-loading-mask.active {
  opacity: .7;
  pointer-events: all;
}

.oui-loading.active {
  opacity: 1;
  pointer-events: all;
}

