  font-family: SamsungOne-Regular, sans-serif;
  font-weight: 300;
  position: relative;
}

.oui-bubble-item:last-child {
  border-bottom: none;
}

.oui-bubble-item:after {
  content: "";
  width: 100%;
  position: absolute;
  bottom: 1px;
  right: 0;
}

.oui-bubble-item:last-child:after {
  border: none;
}

.oui-bubble-item2 {
  color: inherit;
  touch-action: auto;
  will-change: transform;
  border-bottom: 2px solid #79797933;
  padding: 18px 0;
  font-family: SamsungOne-Regular, sans-serif;
  font-weight: 300;
  position: relative;
}

.oui-bubble-item2:last-child {
  border-bottom: none;
}

.oui-bubble-item2:after {
  content: "";
  width: 100%;
  position: absolute;
  bottom: 1px;
  right: 0;
}

.oui-bubble-item2:last-child:after {
  border: none;
}

.oui-overlay-bubble-item {
  color: var(--text-secondary);
  align-items: center;
  gap: 5px;
  padding-top: 17px;
  padding-bottom: 3px;
  display: flex;
