.oui-overlay-bubble-info {
  color: var(--text-secondary);
  touch-action: auto;
  align-items: center;
  margin-top: 10px;
  display: flex;
  position: relative;
  top: -3px;
}

.oui-overlay-bubble-info.pressed-effect {
  transition: transform .25s cubic-bezier(.33, 1, .68, 1);
  transform: scale(.87);
}

.oui-overlay-bubble-info.released-effect {
  transition: transform .5s cubic-bezier(.25, 1.5, .5, 1);
  transform: scale(1);
}

.sub-text {
  color: var(--sub-text-accent);
  padding-top: 10px;
  padding-left: 15px;
  font-family: SamsungSharpSans-Bold, sans-serif;
  font-weight: normal;
  font-size: 15px !important;
}

.oui-icon {
  vertical-align: middle;
  fill: currentColor;
  transform-origin: center;
  flex-shrink: 0;
  display: inline-block;
  position: relative;
  top: -2px;
  transform: scale(.9);
}

.oui-icon-option {
  vertical-align: middle;
  fill: currentColor;
  transform-origin: 0 0;
  flex-shrink: 0;
  margin-right: 5px;
  display: inline-block;
  transform: scale(.88);
}

.oui-overlay-bubble-info.open-dialog-btn {
